      setInterval(this.add_piedata, 3000);	// 每三秒更新实时数据到折线图
      
      window.onresize = ()=>{
        this.resize_windows();
      }
    
      
     
  },

methods: {
  
    resize_windows: function(){
      
        // 第六步，执行echarts自带的resize方法，即可做到让echarts图表自适应
        this.myChart.resize();
        this.averageChart.resize();
        this.pieChart.resize();
        // 如果有多个echarts，就在这里执行多个echarts实例的resize方法,不过一般要做组件化开发，即一个.vue文件只会放置一个echarts实例
        /*
        this.myChart2.resize();
        this.myChart3.resize();
        ......
        */

    },
    
     playVid:function()
    { 
      
    
    
      // document.getElementById("video1").load()
  var myVideo=document.getElementById("video1"); 
  if(this.start_pause==true){
    myVideo.play(); 
  this.video_state="暂停"
  this.start_pause=false;
  }
  else{
    myVideo.pause(); 
  this.video_state="Start"
  this.start_pause=true;
  }
  
    },
    resetVid:function()
    { 
      
    
      
      document.getElementById("video1").load()
  var myVideo=document.getElementById("video1"); 
  this.sensor1=[];
  this.sensor2=[];
  this.sensor3=[];
  this.sensor4=[];
  this.sensor5=[];
  this.sensor6=[];
  this.sensor7=[];
  this.sensor8=[];
  this.average_sensor=[];
  this.myChart.clear();
  this.averageChart.clear();
  this.pieChart.clear();
  myVideo.pause(); 
    },
    addData : function() {
        // 从接口获取数据并添加到数组
        var myVideo=document.getElementById("video1"); 
        var value=myVideo.currentTime;
        var time=parseInt(value);
        var random_value=0.03;
        // var time_step=this.time_step;
        if(this.start_pause==false&&time<30&&time>0.5){
          this.sensor1.push((this.sensor_excel[0][time]+Math.random()*random_value).toFixed(3));
        this.sensor2.push((this.sensor_excel[1][time]+0.05+Math.random()*random_value).toFixed(3));
        this.sensor3.push((this.sensor_excel[2][time]+0.01+Math.random()*random_value).toFixed(3));
        this.sensor4.push((this.sensor_excel[3][time]+0.015+Math.random()*random_value).toFixed(3));
        this.sensor5.push((this.sensor_excel[4][time]+0.05+Math.random()*random_value).toFixed(3));
        this.sensor6.push((this.sensor_excel[5][time]+0.015+Math.random()*random_value).toFixed(3));
        this.sensor7.push((this.sensor_excel[6][time]+0.02+Math.random()*random_value).toFixed(3));
        this.sensor8.push((this.sensor_excel[7][time]+0+Math.random()*random_value).toFixed(3));
        
        }
        

        
        // if(time_step>30){
        //     this.sensor1.shift();
        //     this.sensor2.shift();
        //     this.sensor3.shift();
        //     this.sensor4.shift();
            
        //     this.date.push(time_step);
        //     this.date.shift();
        // }
        
        // this.time_step=this.time_step+1;
        
        // 重新将数组赋值给echarts选项
        this.echartsOption.xAxis.data = this.date;
        this.echartsOption.series[0].data = this.sensor1;
        this.echartsOption.series[1].data = this.sensor2;
        this.echartsOption.series[2].data = this.sensor3;
        this.echartsOption.series[3].data = this.sensor4;
        this.echartsOption.series[4].data = this.sensor5;
        this.echartsOption.series[5].data = this.sensor6;
        this.echartsOption.series[6].data = this.sensor7;
        this.echartsOption.series[7].data = this.sensor8;
     
        
        this.myChart.setOption(this.echartsOption,true);
          
      },
    addAverageData : function() {
        // 从接口获取数据并添加到数组
          
        var myVideo=document.getElementById("video1"); 
        var value=myVideo.currentTime;
        var time=parseInt(value);
        
        // var time_step=this.time_step;
        if(this.start_pause==false&&time<30&&time>0.5){
          this.average_sensor.push((this.sensor_excel[8][time]).toFixed(3));
        
        }
        
        
        
        

        // var time_step=this.average_time_step
        // if(time_step>30){
            
        //     this.average_sensor.shift();

        //     this.average_date.push(time_step);
        //     this.average_date.shift();
        // }
        
        // this.average_time_step=this.average_time_step+1;
        
        // 重新将数组赋值给echarts选项
        this.average_option.xAxis.data = this.average_date;
        this.average_option.series[0].data = this.average_sensor;
      
        
        this.averageChart.setOption(this.average_option);
          
      },
      add_piedata : function() {
        // 从接口获取数据并添加到数组
          
        var myVideo=document.getElementById("video1"); 
        var value=myVideo.currentTime;
        var time=parseInt(value);
        var random_value=0;
        // var time_step=this.time_step;
        if(this.start_pause==false&&time<30&&time>0.5){
          this.pie_option.series[0].data[0].value = (this.pie_excel[0][time]+Math.random() * random_value).toFixed(3);
        this.pie_option.series[0].data[1].value = (this.pie_excel[1][time]+Math.random() * random_value).toFixed(3);
        this.pie_option.series[0].data[2].value = (this.pie_excel[2][time]+Math.random() * random_value).toFixed(3);
        
        }
        
        
        // 重新将数组赋值给echarts选项
        
       
      
        
        this.pieChart.setOption(this.pie_option,true);
          
      }
   
  }
}
</script>
<style>
#myChart {
  width: 100%;
height: 300px;
margin: 0 auto;
};

#averageChart {
  width: 100%;
height: 300px;
margin: 0 auto;
}
</style>